.tags {
    $this: &;
    text-align: center;

    &__tag {
        font-size: 8px;
        line-height: 1.4;
        display: inline-block;

        & + & {
            position: relative;
            margin-left: 27px;

            @include desktop {
                margin-left: 30px;
            }

            &:before {
                content: '';
                position: relative;
                display: inline-block;
                top: 1px;
                left: -14px;
                height: 8px;
                width: 1px;
                background-color:var(--color-black);

                @include desktop {
                    left: -17px;
                }
            }
        }

        @include desktop {
            font-size: 10px;
        }

        &::after {
            width: 97%;
        }
    }

    .article-layout__container & {
        @include desktop {
            grid-column: col / span 10;
            grid-column-start: 2;
        }

    }
}
