.generic-card {
    $this: &;

    @include link-reset;

    display: block;

    &__media {
        @include objectFitImageParent;

        position: relative;
        overflow: hidden;
        transition: transform 0.3s ease-out;

        &::before {
            content: '';
            display: block;
            padding-top: percentage(350/304);

            @include desktop {
                padding-top: calc((100vw - var(--scrollbar) - var(--sidebar-width)) * 0.245);
            }
        }

        img {
            transition: transform 0.3s ease-out;
        }

        .image-gallery__card:nth-child(odd) & {
            .generic-slider--without-filters &,
            .generic-slider--image-gallery & {
                &::before {
                    padding-top: percentage(350/304);

                    @include desktop {
                        padding-top: percentage(740/532);
                    }

                }
            }
        }

        .image-gallery__card:nth-child(even) & {
            .generic-slider--without-filters &,
            .generic-slider--image-gallery & {
                &::before {
                    padding-top: percentage(240/304);

                    @include tablet {
                        padding-top: percentage(508 /675);
                    }
                }
            }
        }

        @include desktop {
            .generic-slider__card:first-child & {
                .generic-slider--with-filters & {
                    &::before {
                        padding-top: calc((100vw - var(--scrollbar) - var(--sidebar-width)) * 0.245);
                    }
                }
            }
        }
    }

    &__content {
        margin-top: 24px;
        padding: 0 20px;
        color: var(--color-main-dark);

        @include desktop {
            margin-top: 35px;
            padding: 0 20px;
        }

        .image-gallery__card.tns-slide-active & {
            margin-left: calc(#{galleryCol(1)} - var(--container-gap));
        }

        .image-gallery__card.tns-slide-active + .image-gallery__card.tns-slide-active & {
            margin-left: 0;
        }
    }

    &__subtitle {
        margin-bottom: 8px;
        font-family: $ff-helvetica-neue;
        font-size: 8px;
        font-weight: $fw-regular;
        letter-spacing: 1.6px;
        text-transform: uppercase;

        @include desktop-medium {
            margin-bottom: 10px;
            font-size: 10px;
            line-height: 1.2;
            letter-spacing: 2px;
        }
    }

    &__title {
        position: relative;
        width: fit-content;
        font-family: $ff-financier-display;
        font-size: 22px;
        font-weight: $fw-regular;
        line-height: 1.2;
        letter-spacing: 1px;
        display: inline;

        @include linkhover(0);

        @include desktop-medium {
            font-size: 30px;
            line-height: calc(35 / 30);
        }
    }

    &__sup {
        font-family: $ff-financier-display;
        font-size: 16px;
        line-height: 1.2;
        font-style: italic;
        margin-top: 5px;

        @include desktop-medium {
            font-size: 18px;
        }

    }

    .generic-slider--with-filters &,
    .generic-slider--without-filters & {
        @include hover-focus {
            #{$this}__title {
                background-size: 100% 1px;
            }

            & #{$this}__media img {
                transform: scale(1.05);
            }
            & #{$this}__media {
                transform: scale(0.99);
            }
        }
    }
}
