.article-cards-grid {
    grid-template-columns: 1fr;
    display: grid;
    gap: var(--main-grid-gap);

    @include tablet {
        grid-row-gap: 33px;
        grid-template-columns: 1fr 1fr;
    }

    @include desktop {
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-row-gap: 34px;
    }

    &--article-layout {
        margin-bottom: 40px;

        .container > &:last-child {
            margin-bottom: 62px;

            @include desktop {
                margin-bottom: 112px;
            }

        }
    }
}
