.generic-slider {
    $this: &;

    max-width: 100%;
    overflow: hidden;

    &--with-filters {
        margin-top: -42px;

        @include until-desktop {
            margin-top: 0;
        }

        @include desktop-medium {
            margin-top: 0;
        }
    }

    &__head {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 33px;

        #{$this}--with-filters & {
            margin-bottom: 35px;

            @include until-desktop {
                margin-top: 30px;
            }

            @include desktop-medium {
                margin-bottom: 33px;
            }
        }

        #{$this}--without-filters & {
            align-items: flex-start;
            margin-bottom: 29px;
            width: 100%;

            @include desktop-medium {
                margin-bottom: 33px;
            }
        }
    }

    &__inner {
        @include until-desktop {
            display: flex;
            flex-direction: column;
            width: 100%;
        }

        #{$this}--image-gallery & {
            padding-top: 75px;
            padding-bottom: 70px;

            @include desktop-medium {
                padding-top: 115px;
                padding-bottom: 110px;
            }
        }

        #{$this}--without-filters & {
            padding-top: 70px;
            padding-bottom: 70px;

            @include desktop-medium {
                padding-top: 95px;
                padding-bottom: 110px;
            }
        }


        #{$this}--with-filters & {
            margin-right: 0;
            margin-left: 0;
        }

    }

    &__controls {
        grid-template-columns: auto auto;
        display: grid;
        width: max-content;
        gap: 20px;
        margin-left: auto;

        #{$this}--image-gallery & {
            margin-top: 33px;

            @include desktop-medium {
                margin-top: 60px;
                margin-bottom: 0;
            }
        }

        #{$this}--with-filters & {
            @include desktop-medium {
                margin-top: 9px;
            }
        }

        #{$this}--without-filters & {
            margin-top: 0;

            @include desktop-medium {
                margin-top: 9px;
            }
        }

        @include desktop-medium {
            margin-top: 0;
            margin-bottom: 20px;
        }
    }

    &__title {
        font-family: $ff-financier-display;
        font-size: 30px;
        font-weight: $fw-light;
        line-height: 1.2;

        @include desktop-medium {
            font-size: 40px;
        }

        #{$this}--with-filters & {
            display: none;
        }
    }

    &__slider-container {
        margin-right: 0;
        margin-left: 0;

        @include desktop {
            max-width: none;
            margin-left: 0;
            margin-right: calc(var(--main-grid-gap) * -1);
        }

        #{$this}--with-filters & {
            margin-right: calc(var(--container-gap) * -1);
            margin-left: calc(var(--container-gap) * -1);
            padding: 0 var(--container-gap);

            @include desktop {
                margin-left: 0;
                margin-right: calc(var(--main-grid-gap) * -1);
                padding: 0;
            }
        }

    }

    &__list {
        display: flex;

        #{$this}--without-filters &,
        #{$this}--image-gallery & {
            align-items: center;
        }
    }

    &__card {
        box-sizing: border-box;
        padding-right: var(--main-grid-gap);
        min-width: calc(100% + var(--main-grid-gap));
        max-width: calc(100% + var(--main-grid-gap));

        @include tablet {
            min-width: #{galleryCol(4)};
            max-width: #{galleryCol(4)};
        }

        @include desktop {
            min-width: percentage(3/12);
            max-width: percentage(3/12);
        }

        @include desktop-medium {
            padding-right: var(--main-grid-gap);
        }

        &:nth-child(odd) {
            #{$this}--without-filters &,
            #{$this}--image-gallery & {
                @include desktop {
                    min-width: galleryCol(4);
                    max-width: galleryCol(4);
                }
            }
        }

        &:nth-child(even) {
            #{$this}--without-filters &,
            #{$this}--image-gallery & {
                @include desktop {
                    min-width: galleryCol(5);
                    max-width: galleryCol(5);
                }
            }
        }

        #{$this}--without-filters &,
        #{$this}--image-gallery & {
            height: fit-content;
        }

        #{$this}--with-filters & {
            &:first-child {
                @include desktop {
                    min-width: percentage(6/12);
                    max-width: percentage(6/12);
                }
            }
        }
    }

    &--without-filters {
        background-color: $color-beige;
    }
}
