.top-nav {
    display: none;

    @include desktop {
        z-index: 0;
        position: sticky;
        top: 0;
        left: 0;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        width: 100%;
        max-width: var(--container-width);
        margin: 0 auto;
        padding: 37px 0 27px;
        padding-left: var(--container-gap);
        padding-right: var(--container-gap);
        height: 88px;
        overflow: visible;
    }

    &__breadcrumbs {
        @include desktop {
            width: 50%;
            padding-right: 60px;
        }
    }

    &__the-view-logo {
        @include desktop {
            position: absolute;
            left: 50%;
            transform: translateX(-50%) translateY(3px);
            display: flex;
            justify-content: center;

        }
    }

    &__date {
        @include desktop {
            width: 50%;
            padding-left: 60px;
            text-align: right;
            font-family: $ff-helvetica-neue;
            font-weight: 300;
            font-size: 10px;
            text-transform: uppercase;
            letter-spacing: 2px;
        }
    }
}
