.image-gallery {
    $this: &;
    max-width: 100%;

    &__controls {
        grid-template-columns: auto auto;
        display: grid;
        width: max-content;
        gap: 20px;
        margin-left: auto;


        .generic-slider--image-gallery & {
            margin-top: 33px;

            @include desktop-medium {
                margin-top: 60px;
                margin-bottom: 0;
            }
        }

        .generic-slider--without-filters & {
            margin-top: 0;

            @include desktop-medium {
                margin-top: 9px;
            }
        }

        @include desktop-medium {
            margin-top: 0;
            margin-bottom: 20px;
        }
    }

    &__controls-container {
        display: flex;
        flex-direction: column;
        width: 100%;
        align-items: flex-end;
    }

    &__list {
        display: flex;
        width: 100%;
        align-items: center;
        margin-left: calc((#{galleryCol(1)} - var(--container-gap))* -1);

        @include desktop {
            margin-left: calc((#{galleryCol(2)} - var(--container-gap)) * -1);
        }
    }

    &__card {
        box-sizing: border-box;
        padding-right: var(--main-grid-gap);
        width: var(--container-width);
        height: fit-content;

        @include desktop {
            padding-right: var(--main-grid-gap);
        }

        &:nth-child(odd) {
            width: galleryCol(4);
        }

        &:nth-child(even) {
            width: galleryCol(4);

            @include tablet {
                width: galleryCol(5);
            }
        }
    }
}
