.text-block {
    $this: &;
    padding-top: 24px;
    padding-bottom: 24px;

    @include desktop {
        @include base-grid;
        padding-top: 65px;
        padding-bottom: 65px;
    }

    &__col {
        @include desktop {
            grid-column: col / span 6;
            grid-column-start: 5;
        }
    }

    &--two-columns {
        @include desktop {

            #{$this}__col {
                grid-column: col / span 8;
                grid-column-start: 4;
            }
        }

        @include desktop-medium {
            #{$this}__text {
                columns: auto 2;
                gap: 40px;
            }
        }
    }

    &__title {
        font-family: $ff-financier-text;
        font-weight: $fw-regular;
        font-size: 25px;
        line-height: 1.2;
        margin-bottom: 15px;

        @include desktop {
            font-size: 30px;
            line-height: 1.3;
            margin-bottom: 32px;
        }

        i {
            display: block;
        }
    }
}
