.breadcrumbs {
    $this: &;

    &__list {
        display: flex;
        flex-wrap: wrap;
    }

    &__item {
        display: flex;
        align-items: center;
        white-space: nowrap;

        &:nth-child(3) {
            width: 100%;
            padding-top: 2px;
        }
    }

    &__link {
        font-size: 10px;
        letter-spacing: 2px;
        font-weight: $fw-light;

        @include linkhover-oneline($color-black, 0);
    }

    &__current {
        font-size: 10px;
        font-family: $ff-helvetica-neue;
        text-transform: uppercase;
        letter-spacing: 2px;
        font-weight: 400;
        font-style: italic;
    }

    &__icon {
        margin: 0 14px;

        @include desktop {
            margin-top: -3px;
        }

        @include desktop-wide {
            margin-top: -2px;
        }
    }
}
