.cta-block {

    @include default-component-padding;

    @include desktop {
        @include base-grid;
    }

    &__title {
        font-family: $ff-financier-display;
        font-weight: $fw-light;
        font-size: 30px;
        line-height: 1.16;
        margin-bottom: 18px;

        @include desktop {
            grid-column: col / span 3;
            grid-column-start: 2;
            font-size: Min(40px, 2.08vw);
            margin-bottom: 0;
        }
    }

    &__info {
        font-family: $ff-financier-display;
        font-weight: $fw-light;
        font-style: italic;
        font-size: 20px;
        line-height: 1.5;
        margin-bottom: 35px;

        @include desktop {
            font-size: Min(22px, 1.14vw);
            line-height: 1.45;
            padding-top: 2px;
            margin-bottom: 0;

            grid-column: col / span 5;
            grid-column-start: 5;
        }

        @media (min-width: 1200px) {
            grid-column: col / span 4;
            grid-column-start: 5;
        }

        @media (min-width: 1500px) {
            grid-column: col / span 5;
            grid-column-start: 5;
        }
    }

    &__btn-area {
        @include desktop {
            grid-column: none;
            grid-column-start: 10;

            display: flex;
            justify-content: flex-end;
            align-items: flex-start;
            width: fit-content;
            padding-top: 10px;
        }

        @media (min-width: 1200px) {
            grid-column: col / span 3;
            grid-column-start: 9;
        }

        @media (min-width: 1500px) {
            grid-column: col / span 2;
            grid-column-start: 10;
        }
    }
}
