.video {
    $this: &;

    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;

    &::before {
        content: '';
        display: block;
        padding-bottom: 61.25%;

        @include desktop {
            padding-bottom: 39%;
        }
    }

    &__media {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__fullbleed-btn {
        @include reset-button;

        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        cursor: pointer;
    }

    &__element,
    &__element + iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        &:not(iframe) {
            object-fit: cover;
        }
    }

    &__fake-btn {
        position: absolute;
        opacity: 1;
        visibility: visible;
        z-index: 5;
        pointer-events: none;
    }

    &__btn.is-play {
        opacity: 0;
    }

    @include hover-focus {
        &::after {
            opacity: 0.2;
        }
    }

    &.is-play {
        cursor: default;
    }
}
