.category-block {
    $this: &;
    padding-bottom: 5px;
    padding-top: 60px;


    @include desktop {
        @include base-grid;
        padding-bottom: 80px;
        padding-top: 120px;
    }

    &__col {
        @include desktop {
            grid-column: col / span 7;
            grid-column-start: 1;
            order: 1;
        }
    }

    &__col-with-title {
        @include desktop {
            grid-column: col / span 5;
            grid-column-start: 8;
            order: 2;
        }
    }

    &__title-area {
        @include desktop {
            position: relative;
            overflow: hidden;
            &::before {
                content: '';
                display: block;
                padding-top: 34.2%;
            }
        }
    }

    &--three-columns {
        @include desktop {

            #{$this}__col,
            #{$this}__col-with-title {
                grid-column: col / span 4;
                grid-column-start: auto;
            }
            #{$this}__col-with-title {
                order: 1;
            }
            #{$this}__title-area {
                &::before {
                    padding-top: 43.2%;
            }
        }
        }
    }
}

.catagory-block-container {
    & + & {
        .category-block {
            border-top: 1px solid $color-black;
        }
    }
}

