.article-card {
    $this: &;

    @include link-reset;

    &__media {
        @include objectFitImageParent;

        position: relative;
        overflow: hidden;
        z-index: 0;
        transition: transform 0.3s ease-out;

        &::before {
            content: '';
            display: block;
            padding-top: percentage(450/389);
        }

        img {
            transition: transform 0.3s ease-out;
        }
    }

    &__icon-wrap {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 40px;
    }

    &__content {
        overflow: hidden;
        margin-top: 14px;
        font-size: 16px;
        padding: 10px 20px 6px;

        @include desktop {
            padding: 20px;
        }
    }

    &__title {
        overflow: hidden;
        display: inline;
        font-size: 25px;
        line-height: 1.16;
        font-weight: $fw-light;
        font-family: $ff-financier-display;

        @include linkhover;

        @include desktop {
            font-size: Min( 30px, 1.72vw);
        }
    }

    &__label {
        font-size: 8px;
        font-family: $ff-helvetica-neue;
        font-weight: $fw-regular;
        line-height: 1.4;
        letter-spacing: 2px;
        text-transform: uppercase;
        margin-bottom: 9px;

        @include desktop {
            margin-bottom: 13px;
        }
    }

    @include hover-focus {
        & #{$this}__title {
            background-size: 100% 1px;
        }
        & #{$this}__media img {
            transform: scale(1.05);
        }
        & #{$this}__media {
            transform: scale(0.99);
        }
    }

    .category-block & {
        #{$this}__content {
            padding-bottom: 50px;
            @include desktop {
                padding: 20px;
            }
        }

        @include hover-focus {
            & #{$this}__media img {
                transform: scale(1.05);
            }
            & #{$this}__media {
                transform: scale(0.99);
            }
        }

    }

    &.card1 {
        #{$this}__media {
            &::before {
                content: '';
                display: block;
                padding-top: percentage(280/304);

                @include desktop {
                    padding-top: percentage(600/675);
                }

                .category-block--three-columns & {
                    @include desktop {
                        padding-top: percentage(600/532);
                    }
                }
            }
        }
    }

    &.card2 {
        #{$this}__media {
            &::before {
                content: '';
                display: block;
                padding-top: percentage(400/304);

                @include desktop {
                    padding-top: percentage(830/961);
                }

                .category-block--three-columns & {
                    @include desktop {
                        padding-top: percentage(830/532);
                    }
                }

            }
        }
    }

    &.card3 {
        #{$this}__media {
            &::before {
                content: '';
                display: block;
                padding-top: percentage(400/304);

                @include desktop {
                    padding-top: percentage(830/532);
                }
            }
        }
    }

    .articles-list & {
        #{$this}__content {
            padding-bottom: 8px;
        }
        @include desktop {
            #{$this}__content {
                padding-bottom: calc(20px - min(11px, 1.2vw));
            }
        }
    }
}
