.two-images {
    $this: &;
    @include base-grid;
    margin-left: -35px;
    margin-right: -35px;

    @include desktop {
        align-items: end;
        margin-right: 0;
        margin-left: 0;
    }

    &__wrapper {
        padding-top: 32px;
        padding-bottom: 32px;

        @include desktop {
            padding-top: 75px;
            padding-bottom: 75px;
        }
    }

    &__col1 {
        grid-column: col / span 6;
        grid-column-start: 1;

        @include desktop {
            grid-column: col / span 4;
            grid-column-start: 3;
        }

        @include objectFitImageParent;

        position: relative;
        overflow: hidden;
        z-index: 0;

        &::before {
            content: '';
            display: block;
            padding-top: percentage(790/532);
        }

        #{$this}--one & {
            grid-column: col / span 12;
            grid-column-start: 1;

            @include desktop {
                grid-column: col / span 8;
                grid-column-start: 3;
            }
            &::before {
                padding-top: percentage(787/1104);
            }
        }

    }

    &__col2 {
        grid-column: col / span 6;
        grid-column-start: 7;

        @include desktop {
            grid-column: col / span 4;
            grid-column-start: 7;
        }

        #{$this}--one & {
            display: none;
        }

        @include objectFitImageParent;

        position: relative;
        overflow: hidden;
        z-index: 0;

        &::before {
            content: '';
            display: block;
            padding-top: percentage(790/532);
        }
    }

    &__col3 {
        grid-column: col / span 12;
        grid-column-start: 1;
        @include desktop {
            grid-column: col / span 2;
            grid-column-start: 11;
        }
    }

    &__col3-inner {
        margin: 0 35px;
        @include desktop {
            margin: 0;
        }
    }

    &__text-area {
        margin-top: 20px;
        max-width: 267px;
        line-height: 1;

        @include desktop {
            max-width: 106px;
            margin-top: 0;
            margin-bottom: 40px;
        }

        &:nth-child(1) {
            @include desktop {
                margin-bottom: 22px;
            }
        }

        &:nth-child(2) {
            margin-top: 10px;

            @include desktop {
                margin-top: 0;
            }
        }
    }

    &__title {
        font-size: 10px;
        line-height: 1.6;
        letter-spacing: 2px;
        font-family: $ff-helvetica-neue;
        font-weight: $fw-medium;
        text-transform: uppercase;
        padding-right: 3px;

        @include desktop {
            display: block;
            padding-right: 0;
        }
    }

    &__text {
        font-size: 13px;
        line-height: 1.23;
        font-style: italic;
        font-family: $ff-financier-display;
        font-weight: $fw-regular;

        @include desktop {
            display: block;
        }
    }

    &--within-group {
        padding-top: 0;
        padding-bottom: 0;


    }
}


