.articles-list {
    @include tablet {
        @include base-grid;
        grid-row-gap: 60px;
    }

    @include desktop {
        grid-row-gap: 142px;
    }

    &__item {
        width: 100%;
        margin-bottom: 40px;

        @include tablet {
            margin-bottom: 0;
        }
    }

    & > div {
        display: flex;
        align-items: center;

        .article-card {
            width: 100%;
        }

        @include mobile-only {
            &:nth-child(odd) {
                .article-card__media:before {
                    padding-top: percentage(400/304);
                }
            }

            &:nth-child(even) {
                .article-card__media:before {
                    padding-top: percentage(280/304);
                }
            }
        }

        &:nth-child(4n+1) {
            @include tablet {
                grid-column: col / span 5;
                grid-column-start: 2;
            }
        }

        &:nth-child(4n+2) {
            @include tablet {
                grid-column: col / span 3;
                grid-column-start: 8;
            }
        }

        &:nth-child(4n+3) {
            @include tablet {
                grid-column: col / span 3;
                grid-column-start: 3;
            }
        }

        &:nth-child(4n+4) {
            @include tablet {
                grid-column: col / span 5;
                grid-column-start: 7;
            }
        }
    }

    &__btn {
        text-transform: uppercase;
    }

    &__btn-wrapper {
        display: flex;
        justify-content: center;
        padding-bottom: 18px;
        padding-top: 3px;

        @include tablet {
            padding-top: 41px;
            padding-bottom: 19px;
        }

        @include desktop {
            padding-top: 92px;
            padding-bottom: 19px;
        }
    }
}

.articles-list-wrapper {
    padding: 75px 0 57px 0;

    @include desktop {
        padding: 170px 0 152px 0;
    }
}

.articles-list-title {
    @include tablet {
        @include base-grid;
        grid-row-gap: 60px;
    }

    @include desktop {
        grid-row-gap: 142px;
    }

    &__text {
        grid-column: col/span 12;
        grid-column-start: 2;
        margin-bottom: 29px;
        font-family: $ff-financier-display;
        font-size: 30px;
        font-weight: $fw-light;
        line-height: 1.2;

        @include desktop-medium {
            margin-bottom: 49px;
            font-size: 40px;
        }
    }
}
