.featured-article {
    margin-bottom: 15px;
    @include desktop {
        @include base-grid;
        align-items: center;
        margin-bottom: 54px;
    }

    &__col1 {
        margin-bottom: 58px;

        @include desktop {
            grid-column: col / span 3;
            grid-column-start: 2;
            margin-bottom: 0;
        }
    }

    &__col2 {
        @include desktop {
            grid-column: col / span 7;
            grid-column-start: 6;
        }
    }

    &__media {
        @include objectFitImageParent;

        position: relative;
        overflow: hidden;
        z-index: 0;
        // transition: transform 0.3s ease-out;
        margin-right: -35px;

        @include desktop {
            margin-right: 0;
        }

        &::before {
            content: '';
            display: block;
            padding-top: percentage(400/340);
            @include desktop {
                padding-top: percentage(960/985);
            }

        }

    }

    &__featured {
        display: block;
        font-family: $ff-helvetica-neue;
        font-weight: $fw-regular;
        font-size: 8px;
        letter-spacing: 1.6px;
        text-transform: uppercase;
        margin-bottom: 20px;
    }

    &__title {
        font-family: $ff-financier-display;
        font-weight: $fw-light;
        font-size: 35px;
        line-height: 1.14;
        margin-bottom: 15px;

        @include desktop {
            font-size: Min(55px, 2.86vw);
            line-height: 1.14;
            margin-bottom: 27px;
        }
    }

    &__intro {
        font-family: $ff-financier-text;
        font-weight: $fw-regular;
        font-size: 18px;
        line-height: 1.55;
        margin-bottom: 30px;

        @include desktop {
            margin-bottom: 48px;
        }
    }

    &__btn {
        text-transform: uppercase;
    }
}
