@mixin shareBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    background-color: transparent;
    border: none;
    border-radius: 0;
    color: $color-black;
    cursor: pointer;
    margin-bottom: 25px;

    &:active {
        opacity: 0.8;
    }

    svg {
        stroke: $color-black;

        [aria-expanded="true"] & {
            stroke: $color-red;
        }
    }

    @include hover-focus {
        svg {
            stroke: $color-red;
        }
    }

    @include desktop {
        flex-direction: column;
    }
}

.share-btn {
    $this: &;

    &__wrapper {
        position: relative;
    }

    &__toggle-btn {
        @include shareBtn;

        #{$this}--active & {
            position: relative;
            z-index: calc(var(--share-btn-popup-z-index) + 1);
            color: $color-black;
        }

        #{$this}--empty:not(#{$this}--active) & {
            background-color: transparent;
        }
    }

    &__btn-text {
        display: block;
        text-transform: uppercase;
        font-size: 8px;
        font-family: $ff-helvetica-neue;
        font-weight: $fw-regular;
        line-height: 1.5;
        letter-spacing: 0.8px;
        margin-top: 8px;
        @include linkhover;
    }

    @include hover-focus {
        #{$this}__btn-text {
            background-size: 100% 1px;
        }
    }

    &--empty {
        &:not(#{$this}--active) {
            background-color: transparent;
        }
    }
}

.share-btn-wrap {

    font-size: 8px;
    font-family: $ff-helvetica-neue;
    font-weight: $fw-regular;
    line-height: 1.5;
    letter-spacing: 0.8px;
    text-transform: uppercase;
    transition: none;

    &.is-mobile {
        height: 55px;
        @include desktop {
            display: none;
        }

        svg {
            width: 26.22px;
            height: 26.18px;
            margin-right: 10px;
        }

        .share-btn__btn-text {
            margin-top: 2px;
            transition: none;
        }

        border-top: 1px solid $color-black;
        padding: 10px 0 0;
        margin: 0 -35px;

        .share-btn__toggle-btn {
            margin-bottom: 0;
        }
        .share-btn__wrapper {
            margin-top: 2px;
        }
    }
}
