.article-layout {
    $this: &;

    position: relative;

    &__container {
        @include container;
        margin-top: 79px;
        background-color: var(--theme-color-bg);
        padding-top: 0;

        @include desktop {
            margin-top: 0;
        }
    }

    &__intro {
        padding-bottom: 14px;

        @include desktop {
            margin-bottom: 65px;
            padding-bottom: 0;
        }

        #{$this}--no-secondary-img & {
            @include desktop {
                margin-bottom: 49px;
                padding-bottom: 0;
            }
        }
    }

    &__intro-wrapper {
        margin-bottom: 70px;

        @include desktop {
            @include base-grid;
            margin-bottom: 140px;
        }
    }

    &__title {
        font-family: $ff-financier-display;
        font-size: 46px;
        font-weight: 300;
        line-height: 1;
        text-transform: uppercase;
        text-align: center;
        margin-bottom: 25px;

        @include desktop {
            font-size: Min(120px, 6.25vw);
            grid-column: col / span 12;
            grid-column-start: 1;
            margin-bottom: 0;
            padding-bottom: calc(70px - Min(0.25em, 1.56vw));
        }
    }

    &__cols {
        @include desktop {
            @include base-grid;
            align-items: center;
        }

    }

    &__img {
        margin: 0 -35px;
        @include objectFitImageParent;
        position: relative;

        @include desktop {
            margin: 0;
        }

        &::before {
            content: '';
            display: block;
            padding-top: percentage(500/375);
            @include desktop {
                padding-top: percentage(890/675);
            }
        }
    }

    &__col {

        &--left {
            margin-bottom: 60px;

            @include desktop {
                margin-bottom: 0;
                grid-column: col / span 5;
                grid-column-start: 2;
            }
        }

        &--right {
            @include desktop {
                grid-column: col / span 4;
                grid-column-start: 8;
            }
        }
    }

    &__share {
        @include tablet {
            display: none;
        }
    }

    &__credits {
        margin-bottom: 30px;
    }

    &__summary {
        font-family: $ff-financier-display;
        font-size: 26px;
        line-height: 1.3;

        @include desktop {
            font-size: Min(40px, 2.1vw);
        }
    }

    &--property {

        @include desktop {
            padding-top: 10px;
            background-color: var(--theme-color-bg);
        }

        #{$this}__title {
            @include desktop {
                font-size: Min(7.5rem,6.25vw);
                margin-bottom: 0;
                padding-bottom: calc(70px - Min(0.25em, 1.56vw));
            }
        }

        #{$this}__intro {

            @include desktop {
                margin-top: 55px;
            }
        }

        #{$this}__intro-wrapper {
            @include desktop {
                margin-bottom: 44px;
            }
        }

        #{$this}__cols {
            align-items: start;
        }

        #{$this}__col {
            &--right {
                order: 2;
                padding: 0;
                margin-bottom: 60px;

                @include desktop {
                    margin-bottom: 0;
                    grid-column: col / span 5;
                    grid-column-start: 8;
                }
            }

            &--left {
                order: 1;
                margin-bottom: 0;

                @include desktop {
                    grid-column: col / span 4;
                    grid-column-start: 2;
                    margin-top: 0;
                    padding-top: 163px;
                }
            }
        }

        #{$this}__img {
            &--main {
                &::before {
                    padding-top: percentage(405/375);

                    @include desktop {
                        padding-top: percentage(1024/1676);
                    }
                }
            }

            &--secondary {
                margin-top: -52px;
                margin-left: auto;
                width: 69%;

                @include desktop {
                    margin-top: -94px;
                    margin-right: -80px;
                    width: auto;
                }

                &::before {
                    padding-top: percentage(270/259);

                    @include desktop {
                        padding-top: percentage(890/762);
                    }
                }
            }
        }
    }

    &--no-secondary-img {
        #{$this}__cols {
            margin-top: 60px;
        }

        #{$this}__col {
            &--right {
                margin: 0;
            }

            &--left {
                margin-top: 0;
                @include desktop {
                    padding-top: 102px;
                    grid-column: col/span 6;
                    grid-column-start: 2;
                }
                @include desktop-medium-spec {
                    grid-column: col/span 4;
                    grid-column-start: 2;
                }
            }
        }
    }

    &--category-page {
        background-color: var(--theme-color-bg);

        @include desktop {
            padding-top: 10px;
        }

        #{$this}__title {
            font-size: 55px;
            margin-bottom: 30px;

            @include desktop {
                margin-bottom: 0;
                font-size: Min(200px, 10.41vw);
                padding-bottom: calc(63px - Min(0.25em, 2.7vw));
            }
        }

        #{$this}__intro-wrapper {
            @include desktop {
                margin-bottom: 80px;
            }
        }

        #{$this}__container {
            margin-top: 84px;

            @include desktop {
                margin-top: 44px;
            }
        }

        #{$this}__summary {
            font-size: 20px;
            font-style: italic;
            font-weight: $fw-light;
            line-height: 1.5;
            text-align: center;

             @include desktop {
                font-size: 22px;
                grid-column: col / span 4;
                grid-column-start: 5;
            }
        }

        #{$this}__img {
            margin: 0 -35px;

            @include desktop {
                margin: 0;
            }

            &::before {
                margin: 0;
                padding-top: percentage(500/375);
                @include desktop {
                    padding-top: percentage(920/1676);
                }
            }
        }

        #{$this}__intro {
            @include desktop {
                margin-top: 0;
                margin-bottom: 0;
                padding-bottom: 0;
            }
        }
    }
}
