.form-page {

    &__title {
        line-height: 1;
        margin-top: 80px;
        margin-bottom: 30px;
        font-size: 45px;
        font-family: $ff-financier-display;
        font-weight: $fw-light;

        @include desktop {
            font-size: Min(100px, 5.2vw);
            margin-top: -17px;
            margin-bottom: 20px;
        }
    }

    &__intro {
        font-size: 20px;
        font-family: $ff-financier-display;
        font-weight: $fw-light;
        font-style: italic;
        line-height: 1.5;
        text-align: center;
        margin-bottom: 40px;

        @include desktop {
            font-size: 22px;
            line-height: 1.45;
            max-width: 532px;
            margin-left: auto;
            margin-right: auto;
            margin-bottom: 47px;
        }
    }

    &__footer-info {
        font-family: $ff-helvetica-neue;
        font-weight: $fw-regular;
        font-size: 10px;
        text-transform: uppercase;
        letter-spacing: 2px;
        line-height: 1.4;
        text-align: center;
        margin-bottom: 72px;

        @include desktop {
            max-width: 527px;
            margin-left: auto;
            margin-right: auto;
            padding-top: 26px;
            padding-bottom: 77px;
        }
    }
}
