.quote-block {
    display: flex;
    flex-direction: column-reverse;
    gap: 38px;
    @include default-component-padding;

    @include desktop {
        @include base-grid;
    }

    &__col1 {
        display: flex;
        justify-content: center;
        align-items: center;

        @include desktop {
            grid-column: col / span 3;
            grid-column-start: 2;

            .quote-block--no-image & {
                grid-column: col / span 6;
                grid-column-start: 2;
            }
        }
    }

    &__col2 {
        @include objectFitImageParent;
        position: relative;
        overflow: hidden;
        z-index: 0;

        @include desktop {
            grid-column: col / span 7;
            grid-column-start: 6;
        }

        &::before {
            content: '';
            display: block;
            padding-top: percentage(760/961);
        }
    }

    blockquote {
        padding: 0;
        margin: 0;
        max-width: 818px;

        p {
            margin: 0;
            line-height: 1.25;
            font-size: 26px;
            font-family: $ff-financier-display;
            font-weight: $fw-light;

            &::before {
                content: '“';
                display: inline;
                font-family: $ff-financier-display;
                font-weight: $fw-light;
            }

            &::after {
                content: '”';
                display: inline;
                font-family: $ff-financier-display;
                font-weight: $fw-light;
            }

            @include desktop {
                font-size: Min(40px, 2.08vw);
            }
        }
    }

    figure {
        margin: 0 35px;

        @include desktop {
            margin: 0;
        }
    }

    figure figcaption {
        font-family: $ff-helvetica-neue;
        font-size: 10px;
        font-weight: $fw-regular;
        font-style: italic;
        line-height: 1.4;
        text-transform: uppercase;
        letter-spacing: 2px;
        padding-left: 40px;
        margin-top: 25px;
    }
}
