.video-modal-close {
    @include reset-button;
    position: absolute;
    right: 28px;
    top: 28px;
    color: $color-white;
    cursor: pointer;

    @include desktop {
        display: flex;
        flex-direction: column;
        width: 36px;
    }

    &__text {
        display: none;
        @include desktop {
            display: inline-block;
            transform: rotate(90deg) translate(40px, 15px);
            line-height: 1;
            text-decoration: underline;
            text-transform: uppercase;
            letter-spacing: 2px;
            font-family: $ff-helvetica-neue;
            font-weight: $fw-regular;
            font-size: 10px;
            margin-left: 10px;
        }
    }
}
