.section-video {
    $this: &;
    position: relative;
    padding: 72px 0 158px;
    background-color: $color-green;

    @include desktop {
        padding: 125px 0 254px;
    }

    &__title {
        margin-bottom: 70px;
        font-family: $ff-helvetica-neue;
        font-size: 10px;
        font-weight: $fw-regular;
        line-height: 1.2;
        letter-spacing: 2.4px;
        text-align: center;
        text-transform: uppercase;
        color: $color-white;

        @include desktop-medium {
            margin-bottom: 114px;
        }
    }

    &__bg-element {
        position: absolute;
        bottom: 0;
        width: 100%;
        height: calc(50% + 30px / 2);
        padding-bottom: col(1);
        background: var(--theme-color-primary);

        @include desktop {
            height: calc(50% + #{col(1) / 2});
        }
    }

    &__container {
        @include container;

        position: relative;
        max-width: 1700px;

        @include desktop {
            padding-right: col(1);
            padding-left: col(1);
        }
    }
}

.video-in-modal {
    outline: none;
    position: absolute;
    top: 40px;
    bottom: 20px;
    left: 0;
    right: 0;

    margin-top: 50px;
    margin-bottom: 30px;

    @include desktop {
        top: 0;
        margin-top: 80px;
        margin-bottom: 80px;
        margin-right: 80px;
        margin-left: 80px;
    }
}


.video-description {
    position: absolute;
    bottom: 20px;
    padding-left: 20px;

    font-family: $ff-helvetica-neue;
    font-weight: $fw-regular;
    font-size: 8px;
    text-transform: uppercase;
    letter-spacing: 1.4px;

    @include desktop {
        bottom: 50px;
        left: 80px;
        padding-left: 25px;
        font-size: 10px;
        letter-spacing: 2.4px;
    }
}
