.burger-menu {

    $this: &;

    display: flex;
    flex-direction: column;
    flex-grow: 1;

    &__list {
        @include reset-list();
        list-style: none;
        counter-reset: custom-counter;

        @include desktop {
            border-top: 1px solid $color-black;
            margin-top: 17vh;
        }
    }

    &__item {
        position: relative;
        font-size: 30px;
        padding: 12px 20px 20px;
        border-bottom: 1px solid $color-black;
        text-align: center;

        @include desktop {
            display: flex;
            align-items: cflex-start;
            justify-content: center;
            padding: 20px 20px 12px;
        }

    }

    &__link {
        display: block;
        position: relative;
        overflow: hidden;
        outline: none;
        text-decoration: none;
        color: $color-black;
        width: 100%;

        @include desktop {
            padding-left: 20px;
            &:hover {

                #{$this}__title-inner {
                    transform: translateY(0);
                }
            }
        }
    }

    &__title {
        position: relative;
        overflow: hidden;

        font-size: 35px;
        line-height: 1;
        font-family: $ff-financier-display;
        font-weight: $fw-light;
        text-transform: uppercase;

        @include desktop {
            display: inline-block;
            padding-left: 35px;
            height: 0.82em;
            font-size: 60px;
        }

        @include desktop-wide {
            font-size: 90px;
        }

        counter-increment: custom-counter;

        &::before {
            content: '0'counter(custom-counter) '. ';
            display: block;
            color: $color-red;
            font-family: $ff-financier-display;
            font-weight: $fw-light;
            font-size: 20px;
            font-style: italic;
            padding-top: 10px;
            padding-bottom: 9px;

            @include desktop {
                position: absolute;
                top: 8px;
                left: 0;
                padding-bottom: 0;
            }

            @include desktop-wide {
                font-size: 25px;
                top: 15px;
            }
        }


    }

    &__title-inner {
        display: block;
        position: relative;
        z-index: 1;

        &:not(:first-child) {
            display: none;
        }

        @include desktop {
            transform: translateY(-1em);
            transition: transform 0.3s;

            &:not(:first-child) {
                display: block;
            }
        }
    }

    @keyframes MoveUpInitial {
        from {
            transform: translate3d(0,0,0);
        }
        to {
            transform: translate3d(0,100%,0);
        }
    }

    @keyframes MoveUpEnd {
        from {
            transform: translate3d(0,-100%,0);
        }
        to {
            transform: translate3d(0,0,0);
        }
    }

    &__logo-subscribe {
        display: flex;
        justify-content: center;
        gap: 85px;
        margin-top: 53px;

        @include desktop {
            display: none;
        }
    }

    &__subscribe {
        display: block;

        @include desktop {
            display: none;
        }
    }

    &__kf-link {
        display: block;

        @include desktop {
            display: none;
        }
    }

    &__content-wrapper {
        text-align: center;
    }

    &__content {
        @include reset-list();
        margin: 18px auto 30px;
        max-width: 1312px;
        color: $color-black;
        display: flex;
        justify-content: center;

        @include desktop {
            justify-content: space-evenly;
            margin: 54px auto;
        }
    }

    &__content-item {
        text-align: center;
        font-family: $ff-helvetica-neue;
        font-weight: $fw-regular;
        font-size: 10px;
        line-height: 1.4;
        letter-spacing: 2px;
        max-width: 305px;

        &:not(:first-child) {
            display: none;

            @include desktop {
                display: block;
            }
        }
    }

}
