.tag-page {
    position: relative;

    &__title {
        line-height: 1;
        margin-bottom: 35px;
        margin-top: 79px;

        @include desktop {
            font-size: Min(100px, 5.2vw);
            margin-bottom: 0;
            margin-top: 6px;
            padding-bottom: calc(76px - min(0.25em, 1.2vw));
        }
    }

    &__qty-found {
        text-align: center;
        font-size: 8px;
        line-height: 1.4;
        letter-spacing: 2px;
        text-transform: uppercase;
        font-family: $ff-helvetica-neue;
        font-weight: $fw-regular;
        margin-bottom: 73px;

        @include desktop {
            font-size: 10px;
            margin-bottom: 50px;
        }
    }

    &__title-latest {
        font-family: $ff-financier-display;
        font-weight: $fw-light;
        font-size: 30px;
        line-height: 1.25;
        margin-bottom: 28px;

        @include desktop {
            font-size: Min(40px, 2.08vw);
            margin-bottom: 42px;
        }
    }

    &__separator {
        height: 0;
        width: 100%;
        margin: 25px auto 50px;
        border: none;
        border-bottom: 1px solid $color-black;

        @include desktop {
            margin: 74px auto 100px;
        }
    }

    &__breadcrumb {
        display: none;

        @include desktop {
            display: block;
            margin-top: 35px;
        }
    }
}
