.subscribe-link {

    $this: &;
    font-family: $ff-helvetica-neue;
    font-weight: $fw-medium;
    font-size: 11px;
    color: $color-black;
    text-decoration: none;
    text-align: center;

    &--footer {
        margin-bottom: 24px;
    }

    span {
        display: block;
        text-transform: uppercase;
        font-size: 8px;
        font-family: $ff-helvetica-neue;
        font-weight: $fw-regular;
        line-height: 1.5;
        letter-spacing: 0.8px;
        margin-top: 8px;
        color: $color-black;
        @include linkhover;
    }

    svg {
        stroke: $color-black;
    }

    @include hover-focus {
        span {
            background-size: 100% 1px;
        }
        svg {
            stroke: $color-red;
        }
    }

    .is-subscribe-page & {
        cursor: none;
        pointer-events: none;
        svg {
            stroke: $color-red;
        }
    }


}



