.carousel-filters {
    $this: &;

    @include desktop-wide {
        position: absolute;
        display: flex;
        align-items: center;
    }

    .carousel-with-filters & {
        @include desktop-medium {
            position: absolute;
            display: flex;
            align-items: center;
        }
    }

    &__title {
        margin-bottom: 25px;
        font-family: $ff-financier-display;
        font-weight: $fw-light;
        font-size: 30px;
        line-height: 1.2;

        @include desktop-wide {
            margin-right: 45px;
            margin-bottom: 0;
            font-size: 40px;
        }

        .carousel-with-filters & {
            @include desktop-medium {
                margin-right: 45px;
                margin-bottom: 0;
                font-size: 40px;
            }
        }
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        margin: -7px -9px;
        max-width: 1000px;

        @include until-desktop {
            max-width: none;
        }

        @include desktop-medium {
            max-width: none;
            margin-top: -4px;
        }
    }

    &__filter {
        @include btn;

        margin: 7px 9px;
        padding: 10px 20px;
        font-size: 10px;
        letter-spacing: 2px;
        text-transform: uppercase;
        border: 1px solid $color-black;
        border-radius: 30px;
        transition: opacity 0.2s;

        @media (hover: hover) {
            &:hover {
                opacity: 0.5;
            }
        }

        &--inactive {
            opacity: 0.5;
            border-color: rgba($color-black, 0.5)
        }
    }
}
