.home-page-header {
    padding-top: 28px;
    margin-bottom: 44px;

    @include desktop {
        position: fixed;
        top: 0;
        right: var(--sidebar-width);
        left: 0;
        height: var(--home-page-header-height);
        z-index: 0;
        padding: 44px 0;
        margin-bottom: 0;
    }

    &__container {
        display: flex;
        justify-content: center;
    }

    &__logo {
        display: none;

        @include desktop {
            display: block;
            margin-bottom: 29px;
        }
    }
}
