body {
    --grid-columns: 4;
    --container-gap: 35px;
    --container-width: 100vw;
    --slider-container-width: 100vw;

    --offset: 10px;
    --media-height: 233px;
    --page-header-logo-size: 105px;
    --scrollbar: 0px; // stylelint-disable-line length-zero-no-unit
    --viewport-height: 100vh;

    --sidebar-width: 70px;
    --top-menu-height: 65px;

    --home-page-header-height: 217px;

    /* colors */
    --color-black: #{$color-black};
    --color-white: #{$color-white};
    --color-main-dark: #{$color-main-dark};
    --color-beige: #{$color-beige};
    --color-red: #{$color-red};
    --color-grey: #{$color-grey};
    --color-tranparent-black: #{$color-tranparent-black};
    --input-border-color: #{$color-black};
    --main-grid-gap: 20px;

    @include tablet {
        --grid-columns: 8;
        --ofset: 13px;
        --media-height: 350px;
        --slider-container-width: calc(100vw - var(--scrollbar));

    }

    @include desktop {
        --container-width: calc(100vw - var(--scrollbar));
        --slider-container-width: calc(100vw - var(--scrollbar) - var(--sidebar-width));
        --grid-columns: 12;
        --offset: 16px;
        --container-gap: 80px;
        --main-grid-gap: 40px;
        --main-grid-gap: 40px;
    }

    @include desktop-wide {
        --container-width: 1836px;
        --slider-container-width: 1836px;
        --container-gap: 80px;
    }
}
