.category-list {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    white-space: nowrap;
    overflow-x: auto;
    min-height: 1.2em;

    @include desktop {
        margin-bottom: 10px;
    }

    &__list {
        font-size: 0;
    }

    &__item {
        display: inline-block;
        & + & {
            &::before {
                content: '';
                display: inline-block;
                height: 0;
                width: 5px;
                border-top: 1px solid $color-black;
                margin: 0 7px 3px;

                @include desktop {
                    width: 56px;
                    margin: 0 14px 3px;
                }
            }
        }
    }

    &__link {
        text-decoration: none;
        text-transform: uppercase;
        font-family: $ff-helvetica-neue;
        letter-spacing: 2px;
        font-size: 10px;
        line-height: 1.4;

        @include mobile-tiny-only {
            font-size: 8px;
        }

        @include linkhover-oneline($color-black, -3px);

        &::after {
            width: 97%;
        }
    }
}
