.group-of-images {
    display: grid;
    grid-template-columns: 200fr 150fr;
    grid-column-gap: 20px;
    padding-top: 32px;
    padding-bottom: 32px;

    @include desktop {
        @include base-grid;
        align-items: stretch;
        padding-top: 0;
        margin-top: -36px;
        padding-bottom: 75px;
    }

    &__col1 {
        grid-column: span 2;
        @include desktop {
            grid-column: col / span 3;
            grid-column-start: 2;
            display: flex;
            align-items: center;
        }
    }

    &__col2 {
        margin-left: -35px;
        @include desktop {
            grid-column: col / span 4;
            grid-column-start: 6;
            margin-left: 0;
        }
    }

    &__col2-media {
        width: 100%;
        @include objectFitImageParent;

        position: relative;
        overflow: hidden;
        z-index: 0;

        &::before {
            content: '';
            display: block;
            padding-top: percentage(301/198);
            @include desktop {
                padding-top: percentage(750/530);
            }
        }
    }

    &__col3 {
        margin-right: -35px;
        @include desktop {
            grid-column: col / span 3;
            grid-column-start: 10;
            margin-right: 0;
        }
    }

    &__col3-media {
        width: 100%;
        @include objectFitImageParent;

        position: relative;
        overflow: hidden;
        z-index: 0;

        &::before {
            content: '';
            display: block;
            padding-top: percentage(220/157);
            @include desktop {
                padding-top: percentage(558/390);
            }

        }
    }

    &__large-text {
        font-family: $ff-financier-display;
        font-weight: $fw-light;
        font-size: Max(26px, 2.1vw);
        line-height: 1.19;
        margin-bottom: 40px;

        @include desktop {
            font-size: Min(40px, 2.1vw);
        }
    }
}
