.form-checkbox {
    $this: &;

    position: relative;
    display: flex;
    align-items: center;
    cursor: pointer;

    &__control {
        flex-shrink: 0;
        margin-right: 20px;
    }

    &__wrapper {
        display: flex;
        align-items: center;
        min-height: 36px;
    }

    &__content {
        font-size: 10px;
        font-family: $ff-helvetica-neue;
        font-weight: $fw-regular;
        letter-spacing: 2px;
        line-height: 1.4;
    }
}
