.message-area {
    display: block;
    position: absolute;
    margin-top: -20px;

    font-size: 7px;
    font-family: $ff-helvetica-neue;
    font-weight: $fw-regular;
    text-transform: uppercase;
    color: $color-red;
    letter-spacing: 1.4px;
    padding-left: 17px;

    .form-checkbox & {
        bottom: -10px;
        left: 25px;

        @include desktop {
            bottom: 0;
        }
    }
}
