.article-preview {
    $this: &;
    padding: 75px 0;

    @include desktop {
        padding: 170px 0;
    }

    &__inner {
        @include desktop {
            @include base-grid;
            grid-auto-flow: dense;
            align-items: center;
        }
    }

    &__content {
        margin-bottom: 60px;
        @include desktop {
            grid-column: col / span 4;
            grid-column-start: 8;
            margin-bottom: 0;
        }
    }

    &__media {
        @include desktop {
            grid-column: col / span 5;
            grid-column-start: 2;
        }
        @include objectFitImageParent;

        position: relative;
        transition: transform 0.3s ease-out;

        &::before {
            content: '';
            display: block;
            padding-top: percentage(400/304);

            @include desktop {
                padding-top: percentage(820/675);
            }
        }
    }

    &__label {
        display: block;
        margin-bottom: 20px;
        font-family: $ff-helvetica-neue;
        font-weight: $fw-regular;
        font-size: 8px;
        line-height: 1.2;
        letter-spacing: 1.6px;
        text-transform: uppercase;
    }

    &__title {
        margin-bottom: 20px;
        font-size: 35px;
        font-family: $ff-financier-display;
        font-weight: $fw-light;
        line-height: 1.2;

        @include desktop {
            margin-bottom: 35px;
            font-size: Min(55px, 2.8vw);
        }
    }

    &__summary {
        margin-bottom: 35px;
        font-family: $ff-financier-display;
        font-weight: $fw-regular;
        line-height: 1.6;

        @include desktop {
            margin-bottom: 45px;
            font-size: 18px;
        }
    }

    &__link {
        font-size: 12px;
        line-height: 1;
        text-transform: uppercase;
    }
}

