.anim-load {
    opacity: 0;
    transform: translate3D(0, 70px, 0);
    transition: all 0.8s;

    &.lazyload {
        transition: all 0.8s !important;
    }

    &--delayed {
       transition-delay: 0.8s;
    }

    &__ready {
        opacity: 1;
        transform: translate3D(0, 0, 0);

        &.lazyload {
            opacity: 0;
            transform: translate3D(0, 70px, 0);
        }

        &.lazyloaded {
            opacity: 1;
            transform: translate3D(0, 0, 0);
        }
    }
}


