.text-block-media {
    $this: &;

    @include default-component-padding;

    @include desktop {
        @include base-grid;
    }


    &__title {
        font-family: $ff-financier-text;
        font-weight: $fw-regular;
        font-size: 25px;
        line-height: 1.2;
        margin-bottom: 15px;

        @include desktop {
            font-size: 30px;
            line-height: 1.3;
            margin-bottom: 32px;
        }

        i {
            display: block;
        }
    }

    &__media {
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin: 0 -35px 57px -35px;

        @include desktop {
            margin: 0;
            grid-column: col / span 5;
            grid-column-start: 2;
        }

    }

    &__media-inner {
        @include objectFitImageParent;
        position: relative;
        overflow: hidden;
        transition: transform 0.3s ease-out;

        &::before {
            content: '';
            display: block;
            padding-top: percentage(430/375);

            @include desktop {
                padding-top: percentage(770/675);
            }
        }
    }

    &__text-area {
        display: flex;
        flex-direction: column;
        justify-content: center;

        @include desktop {
            grid-column: col / span 4;
            grid-column-start: 8;
        }
    }
}
