.share-popup-list {
    $this: &;

    padding: 0 ;
    background-color: $color-red;
    height: 54px;
    padding: 0 10px;
    display: flex;
    justify-content: center;
    width: calc(100vw - var(--scrollbar));
    margin-bottom: 12px;

    &:after {
        content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 4px;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 9px 7.5px 0 7.5px;
        border-color: $color-red transparent transparent transparent;
    }

    @include desktop {
        width: auto;
        margin-right: 8px;
        margin-bottom: 0;
        display: block;

        &:after {
            left: auto;
            right: 0;
            top: 19px;
            transform: translateX(0);
            bottom: auto;
            border-width: 7.5px 0 7.5px 10px;
            border-color: transparent transparent transparent $color-red;
        }
    }

    &__item {
        padding: 15px;
        display: flex;
        align-items: center;
    }
    &__btn {
        display: inline-block;
        color: $color-white;
        text-decoration: none;
        font-family: $ff-helvetica-neue;
        font-size: 10px;
        letter-spacing: 2px;
        text-transform: uppercase;
        cursor: pointer;
    }

    [data-placement='left'] &,
    [data-placement='right'] & {
        display: flex;
    }
}
