.category-block-info {
    @include desktop {
        position: absolute;
        top: 0;
    }

    &__explore {
        font-size: 8px;
        letter-spacing: 1.6px;
        line-height: 1.5;
        font-family: $ff-helvetica-neue;
        font-weight: $fw-regular;
        text-transform: uppercase;
        margin-bottom: 8px;

        @include desktop {
            margin-bottom: 10px;
        }
    }

    &__title {
        font-size: 55px;
        line-height: 1;
        font-family: $ff-financier-display;
        font-weight: $fw-light;
        text-transform: uppercase;
        margin-bottom: 20px;

        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        max-width: 100%;

        @include mobile-tiny-only {
            font-size: 40px;
        }

        @include desktop {
            font-size: Min(90px, 4.68vw);
            margin-bottom: 0;
        }
    }

    &__link {
        position: relative;
        display: inline-block;
        font-size: 12px;
        letter-spacing: 2.4px;
        line-height: 1.33;
        font-family: $ff-helvetica-neue;
        font-weight: $fw-regular;
        text-transform: uppercase;
        color: $color-black;
        text-decoration: none;

        @include linkhover;

        margin-bottom: 50px;

        @include desktop {
            margin-bottom: 0;
            font-size: Clamp(10px, 0.625vw, 12px);
        }
    }

    &__link-arrow {
        position: absolute;
        top: 4px;
        right: -20px;
    }
}
