.btn {
    $this: &;
    @include btn;

    font-size: 10px;
    font-weight: 500;
    letter-spacing: 2.4px;
    padding: 16px 25px;
    border: 1px solid var(--color-black);

    transition: color 0.2s;

    @include desktop {
        font-size: 12px;
    }

}

.link {
    @include link-reset;

    position: relative;
    font-family: $ff-helvetica-neue;
    text-transform: uppercase;
    letter-spacing: 2px;

    &:after {
        content: '';
        position: absolute;
        width: 100%;
        transform: scaleX(0);
        height: 1px;
        bottom: 0;
        left: 0;
        background-color: var(--color-black);
        transform-origin: bottom right;
        transition: transform 0.25s ease-out;
    }

    &:hover:after {
        transform: scaleX(1);
        transform-origin: bottom left;
    }

    &:active {
        opacity: 0.6;
    }
}

.btn-arrow {
    $this: &;

    @include focusBoxShadowDark;

    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    background: none;
    border: 1px solid $color-black;
    border-radius: 50%;
    color: $color-black;
    cursor: pointer;
    transition: opacity 0.2s;

    &:active {
        opacity: 0.5;
    }

    &.is-disabled {
        pointer-events: none;
        border-color: $color-grey5;
        opacity: 0.5;
    }

    @include desktop-medium {
        width: 35px;
        height: 35px;
    }

    @include hover-focus {
        border-color: $color-grey5;
        opacity: 0.5;

        & > .icon {
            fill: $color-grey5;
        }
    }
}

.btn-animated {
    $this: &;

    display: inline-block;
    max-width: 100%;
    &__span {
        position: relative;
        overflow: hidden;
        display: block;
        height: 1em;
    }

    &__span-inner {
        position: relative;
        display: block;
        line-height: 1;
        transform: translateY(-1em);
        transition: transform 0.4s;
        white-space: nowrap;
        max-width: 100%;
        overflow: hidden;
    }

    @include hover-focus {
        border-color: $color-red;
        color: $color-red;

        #{$this}__span-inner {
            transform: translateY(0);
        }
    }

    &--disabled {
        opacity: 0.5;
        cursor: default;
        pointer-events: none;

        @include hover-focus {
            border-color: $color-black;
            color: $color-black;

            #{$this}__span-inner {
                transform: translateY(-1em);
            }
        }
    }
}
.btn-round {
    $this: &;

    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 60px;
    background-color: #BF0633;
    border-radius: 50%;
    color: var(--color-white);
    font-family: $ff-helvetica-neue;
    font-size: 10px;
    font-weight: $fw-regular;
    line-height: 1.2;
    letter-spacing: 2px;
    text-decoration: none;
    text-transform: uppercase;
    cursor: pointer;

    @include desktop {
        width: 80px;
        height: 80px;
        letter-spacing: 2.4px;
    }

    & span {
        word-break: break-word;
    }
}
